___CSS_LOADER_EXPORT___.push([module.id, `*,
:after,
:before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    height: 100%;
}

body,
html,
#map {
    width: 100%;
    height: 100%;
}

body {
    position: relative;
    min-height: 100%;
    margin: 0;
    padding: 0;
    background-color: #f1f1f1;
}

img {
    display: block;
    width: 100%;
    height: auto;
}   

.leaflet-popup-content-wrapper{
    width: 200px;
}

.btn-markers {
    margin: 5px;
    padding: 8px 12px;
    background: #0078A8;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
}

.btn-markers:hover {
    background: #005580;
}

.add-button {
    background: #28a745;
}

.add-button:hover {
    background: #218838;
}

.remove-button {
    background: #dc3545;
}

.remove-button:hover {
    background: #c82333;
}

.leaflet-control-layers-list {
    display: flex;
    flex-direction: column;
    gap: 5px;
}`, "",{"version":3,"sources":["webpack://./src/styles/mapstyles.css"],"names":[],"mappings":"AAAA;;;IAGI,sBAAsB;IACtB,UAAU;IACV,SAAS;AACb;;AAEA;IACI,YAAY;AAChB;;AAEA;;;IAGI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,SAAS;IACT,UAAU;IACV,yBAAyB;AAC7B;;AAEA;IACI,cAAc;IACd,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB,mBAAmB;IACnB,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ","sourcesContent":["*,\n:after,\n:before {\n    box-sizing: border-box;\n    padding: 0;\n    margin: 0;\n}\n\nhtml {\n    height: 100%;\n}\n\nbody,\nhtml,\n#map {\n    width: 100%;\n    height: 100%;\n}\n\nbody {\n    position: relative;\n    min-height: 100%;\n    margin: 0;\n    padding: 0;\n    background-color: #f1f1f1;\n}\n\nimg {\n    display: block;\n    width: 100%;\n    height: auto;\n}   \n\n.leaflet-popup-content-wrapper{\n    width: 200px;\n}\n\n.btn-markers {\n    margin: 5px;\n    padding: 8px 12px;\n    background: #0078A8;\n    color: white;\n    border: none;\n    border-radius: 4px;\n    cursor: pointer;\n    font-size: 14px;\n}\n\n.btn-markers:hover {\n    background: #005580;\n}\n\n.add-button {\n    background: #28a745;\n}\n\n.add-button:hover {\n    background: #218838;\n}\n\n.remove-button {\n    background: #dc3545;\n}\n\n.remove-button:hover {\n    background: #c82333;\n}\n\n.leaflet-control-layers-list {\n    display: flex;\n    flex-direction: column;\n    gap: 5px;\n}"],"sourceRoot":""}]);
